
<template>
  <div id="app">
    <transition name="fade">
    <router-view :appTick="appTick" @upData="updata" @reLogin="reLogin"></router-view>
    </transition>
    <div class="tabBar" v-show="tabBarOff">
      <van-tabbar
        v-model="active"
        active-color="#222222"
        inactive-color="#222222"
        :route="true"
        safe-area-inset-bottom
        :z-index="zIndex"
      >
        <van-tabbar-item to="/home" :replace="true">
          <img class="appImg" slot="icon" slot-scope="props" :src="props.active ? sList.active : sList.normal">{{$t('home')}}
        </van-tabbar-item>
        <van-tabbar-item to="/demo/selfchoose" :replace="true" v-if="$route.meta.twoShow">
          <img class="appImg" slot="icon" slot-scope="props" :src="props.active ? hList.active : hList.normal">{{$t('Markets')}}
        </van-tabbar-item>
        <van-tabbar-item to="/demo/market" :replace="true"  v-else>
          <img class="appImg" slot="icon" slot-scope="props" :src="props.active ? hList.active : hList.normal">{{$t('Markets')}}
        </van-tabbar-item>
        <van-tabbar-item to="/trade" :replace="true">
          <img class="appImg" slot="icon" slot-scope="props" :src="props.active ? jList.active : jList.normal">{{$t('Trading')}}
        </van-tabbar-item>
        <van-tabbar-item to="/my" :replace="true">
          <img class="appImg" slot="icon" slot-scope="props" :src="props.active ? wList.active : wList.normal">{{$t('Me')}}
        </van-tabbar-item>
      </van-tabbar>
    </div>
<!--    <div v-if="showTip" class="weww">/(ㄒoㄒ)/~~ <br>行情连接异常，请尝试重新登陆...</div>-->
    <div class="showSelect" v-if="showselect">
      <!--    <div class="showSelect" v-if="true">-->
      <div class="content">
        <div class="title">
          <p v-show="type!==3">检测更新</p>
          <p v-show="type==3">更新成功</p>
          <p v-show="type==3">更新到新版本{{this.version.New}}</p>
          <p v-show="type==0">检测到新版本{{this.version.New}}</p>
          <p v-show="type==1">正在更新下载，请耐心等待</p>
          <p v-show="type==2">更新安装中...</p>
        </div>
        <div class="submit" v-show="type==0">
          <span @click="stopDownload"><span>暂不更新</span></span>
          <span @click="downloadC"><span>立即更新</span></span>
        </div>
        <div class="Progress" v-show="type!==0">
          <div class="ProgressBar" v-show="type!==0"></div>
          <div class="con" v-show="type!==0&&type!==3">正在更新{{ProgressPercent}}</div>
          <div class="con2" v-show="type==3">正在重启...</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import global from './global';
  import pako from 'pako'
  import {mapGetters} from 'vuex'
  import { Toast } from 'vant';
  import EP from './utils/encryption'
  window.onload = function() {
    document.addEventListener('touchstart', function(event) {
      if (event.touches.length > 1) {
        event.preventDefault()
      }
    })
    document.addEventListener('gesturestart', function(event) {
      event.preventDefault()
    })
  }


export default {
  name: 'App',
  data () {
    return {
      tabBarOff:true,
      // 底部导航默认选中
      active: 1,
      allClose:1,
      zIndex:1,
      wsStop:true,
      // 底部导航图标
      sList:{
        normal: require('./assets/app/home.svg'),
        active: require('./assets/app/home1.svg')
      },
      hList:{
        normal: require('./assets/app/demo.svg'),
        active: require('./assets/app/demo1.svg')
      },
      jList:{
        normal: require('./assets/app/trade.svg'),
        active: require('./assets/app/trade1.svg')
      },
      wList:{
        normal: require('./assets/app/my.svg'),
        active: require('./assets/app/my1.svg')
      },
      appTick:[],
      tickCopy:{},
      discUrl:'121.43.137.150',
      sandTime:null,
      // 断线重连计时器
      ReconnectTime:null,
      rTime:null,
      rNum:0,
      time:{},
      showTip:false,
      num:0,
      tickStop:true,
      websocket:null,
      oftenSk:null,
      showScreen:false,
      showselect:false,
      VerData:{},
      version:{
        old:'',
        New:''
      },
      ProgressPercent:'0%',
      type:0
    }
  },
  computed:{
    ...mapGetters([
      'isLogin',
      'diUrl'
    ])
  },
  created() {
    let that = this
    this.discUrl = localStorage.getItem('discUrl')
    localStorage.removeItem('tickCopy');
    localStorage.removeItem('mainList');
    localStorage.duan = 'unDuan'
    if (this.$route.path === "/home") {
      this.active = 0;
    } else if (this.$route.path === "/demo/market") {
      this.active = 1;
    }else if (this.$route.path === "/trade") {
      this.active = 2;
    } else if (this.$route.path === "/my") {
      this.active = 3;
    }
    // 首次连接7777
    let url =this.$store.state.diUrl
    this.localOftenSk(url)
    if(that.global.ws.readyState !== 1 && localStorage.Flag !== undefined && localStorage.abnormal !== 'true'){
      if(localStorage.Logout !== 'Logout'){
        localStorage.duan = 'duan'
        console.log('重连1111')
        that.Reconnect()
        that.localSocket()
      }
    }
    that.global.ws.onclose = function (e) {
      if(localStorage.Logout !== 'Logout'){
        localStorage.duan = 'duan'
        console.log('重连2222')
        that.Reconnect()
      }

    }
    that.global.ws.onerror = function (e) {
      if(localStorage.Logout !== 'Logout'){
        localStorage.duan = 'duan'
        console.log('重连3333')
        that.Reconnect()
      }
    }
    document.addEventListener("plusready", function () {
      document.addEventListener("netchange",onNetChange,false);
      function onNetChange() {
        //获取当前网络类型
        let nt = plus.networkinfo.getCurrentType();
        switch (nt) {
          case plus.networkinfo.CONNECTION_ETHERNET:
          case plus.networkinfo.CONNECTION_WIFI:
            Toast({
              position:'bottom',
              message:'网络环境已切换，正在重新连接中...',
              duration:3000,
              onOpened(){
                setTimeout(function () {
                  console.log('网络切换重连')
                  that.Reconnect()
                },1200)
              },
              onClose () {
                if(that.global.ws.readyState === 1 && that.websocket.readyState === 1 && localStorage.duan === 'unDuan'){
                  Toast({
                    position:'bottom',
                    message:'连接成功',
                    duration:2000
                  })
                }else {
                  Toast({
                    position:'bottom',
                    message:'连接失败，请重新登录',
                    duration:2000,
                    onOpened () {
                      that.Logout()
                    }
                  })
                }
              }
            })
            break;
          case plus.networkinfo.CONNECTION_CELL2G:
          case plus.networkinfo.CONNECTION_CELL3G:
          case plus.networkinfo.CONNECTION_CELL4G:
            Toast({
              position:'bottom',
              message:'网络环境已切换，正在重新连接中...',
              duration:3000,
              onOpened(){
                setTimeout(function () {
                  console.log('网络切换重连')
                  that.Reconnect()
                },1200)
              },
              onClose () {
                if(that.global.ws.readyState === 1 && that.websocket.readyState === 1 && localStorage.duan === 'unDuan'){
                  Toast({
                    position:'bottom',
                    message:'连接成功',
                    duration:2000
                  })
                }else {
                  Toast({
                    position:'bottom',
                    message:'连接失败，请重新登录',
                    duration:2000,
                    onOpened () {
                      that.Logout()
                    }
                  })
                }
              }
            })
            break;
          default:
            Toast({
              message:'网络未连接',
              position:'bottom',
              duration:2000,
              onOpened() {
                that.Logout()
              }
            })
            console.log("当前没有网络");
            break;
        }
      }
      // 运行环境从前台切换到后台事件
      that.textEdition();
      let hiddenTime = ''
      let visibleTime = ''
      document.addEventListener("pause", function () {
        localStorage.setItem('duan','duan')
        hiddenTime = new Date().getTime()
        console.log('息屏了')
      }, false);
      // 运行环境从后台切换到前台事件
      document.addEventListener("resume",function () {
        console.log('亮屏了')
        visibleTime = new Date().getTime();
        if((visibleTime - hiddenTime) / 1000 > 30 && localStorage.Flag !== undefined){
          // 主动关闭连接
          that.global.ws.close(1000,"close")
          that.websocket.close(1000,"close")
          // 1.5S后重连 因为断开需要时间，防止连接早已关闭了
          Toast({
            position:'bottom',
            message:'行情连接中...',
            forbidClick:true,
            duration:3000,
            onOpened(){
              setTimeout(function () {
                console.log('重连4444')
                that.Reconnect()
              },1300)
            },
            onClose() {
              console.log(that.global.ws.readyState,'that.global.ws.readyState')
              console.log(that.websocket.readyState,'that.websocket.readyState')
              // && that.websocket.readyState === 1
              if(that.global.ws.readyState === 1 && that.websocket.readyState === 1 && localStorage.duan === 'unDuan'){
                Toast({
                  position:'bottom',
                  message:'行情连接成功！',
                  forbidClick:true,
                  duration:1500,
                  onClose() {
                    console.log(that.global.ws.readyState,'that.global.ws.readyState1')
                    console.log(that.websocket.readyState,'that.websocket.readyState1')
                  }
                })
              }else {
                Toast({
                  position:'bottom',
                  message:'行情连接失败，请重新登录！',
                  forbidClick:true,
                  duration:2000,
                  onClose() {
                    that.Logout()
                  }
                })
              }
            }
          })
        }else {
          localStorage.setItem('duan','unDuan')
          console.log('还没到')
        }
      }, false);
    }, false);
    that.mui.init({
      keyEventBind: {
        backbutton: true, //关闭back按键监听
      }
    })
    //首页返回键处理
    // 处理逻辑： 1s内，连续两次按返回键，则退出应用；
    let first = null;
    that.mui.back = function(){
      // 首次按键， 提示 再按一次退出应用
      if(!first){
        first = new Date().getTime();//记录第一次按下回退键的时间
        that.mui.toast("再按一次退出应用");
        plus.screen.lockOrientation("portrait-primary")
        console.log(localStorage.back)
        if(localStorage.getItem('back') === 'trade' || localStorage.getItem('back') === 'demo' || localStorage.getItem('back') === 'my' || localStorage.getItem('back') === 'home'){
        }else {
          history.go(-1);
        }
        setTimeout(function(){
          //1s 后清除
          first = null;
        }, 1000);

      }else{
        if(new Date().getTime() - first < 1000){
          //如果两次按下的时间小于1s
          plus.runtime.quit(); //那么就退出app
        }
      }
      plus.screen.lockOrientation("portrait-primary")
    }

    window.addEventListener('offline',function () {
      console.log(that.$router.name)
      console.log(that.$route.name)
      if(that.$route.name !== 'signIn'){
        Toast({
          message:'网络已断开，请检查网络状态！',
          duration:2000,
          position:'bottom',
          onClose() {
            // that.Reconnect()
            that.Logout()
          }
        })
      }
    })
    window.addEventListener('online',function () {
      Toast({
        message:'网络已连接',
        duration:200,
        position:'bottom',
        onClose() {
          // that.$router.go(0)
          that.Reconnect()
        }
      })
    })
  },
  watch:{
    isLogin: function(newVal) {
      if(!this.wsStop){
        // console.log('关闭全局tick');
        this.tickStop = false
        this.close();
        localStorage.removeItem('tickCopy');
        localStorage.removeItem('mainList');
        this.tickCopy={}
        this.showTip=false;
        window.clearInterval(this.time)
      }
      if(newVal===true){
        setTimeout(()=>{
          this.localSocket()
        },0)
        this.tickStop = true
        this.discUrl = localStorage.getItem('discUrl')
      }else {
        this.tickStop = false
      }
    },


    diUrl:function (newVal) {
      console.log('连接',newVal)
      this.localOftenSk(newVal)
    },
    "$route":{
      handler(route){
        const that=this;
        console.log(route.name,'route.name')
        if(route.name === 'detail'){
          that.zIndex = -999
        }else {
          that.zIndex = 1
        }
        this.tabBarOff = !(route.name === 'signIn' || route.name === 'openAccount' || route.name === 'logon');
        if(route.name === 'signIn' || route.name === 'openAccount' || route.name === 'logon' || route.name === 'my' || route.name === 'problem'){
          that.mui.plusReady(function() {
            plus.navigator.setStatusBarStyle('light');
          });
        }else {
          that.mui.plusReady(function() {
            plus.navigator.setStatusBarStyle('dark');
          });
        }
      }
    }

  },
  methods:{
    //app.vue
    updata(){
      this.textEdition(1);
    },
    close(){
      this.websocket.close(1000,"mobile client exit");
    },
    localSocket() {
      let that = this
      this.wsStop = false;
      let time=new Date().getTime();
      let stTime=time.toString();
      console.log(stTime,'stTime')
      let baseS = btoa(stTime)
      console.log(baseS,'baseS')
      this.websocket = new WebSocket("ws://"+this.discUrl+":8189/quoteServer?token="+baseS);
      this.global.setTickWs(that.websocket);
      this.websocket.onmessage=this.Masg;
      this.websocket.onclose = function() {
        if (that.global.ws.readyState === 1 ){
          console.log('行情重连')
          that.localSocket();
        }
      };
      this.websocket.onerror = function() {
          console.log('行情重连')
        if (that.global.ws.readyState === 1 ){
          that.localSocket();
        }

      };
    },
    reLogin(e){
      let url =this.$store.state.diUrl
      this.localOftenSk(url)
    },
    Reconnect() {
      let that = this;
      // console.log(this.$route.name)
        // console.log(that.discUrl)
        that.oftenSk = new WebSocket('ws://'+that.discUrl+':7777/ws');
        that.global.setWs(that.oftenSk);
        that.global.ws.onopen = that.setReconnect
        that.global.ws.onmessage = function(res) {
          that.oftenSkOnmessage (res)
        };
    },
    setReconnect(){
      let actions;
      let that = this
      let LoginMode = localStorage.getItem('LoginMode')
      if(localStorage.environment === 'fictitious'){
        let LoginAccount = localStorage.getItem('LoginAccount')
        let LoginCode = localStorage.getItem('LoginCode')
        let LoginKeying = localStorage.getItem('LoginKeying')
        actions = {
          action: "login",
          reqID: 117,
          data: {
            loginType:2,
            account: '',
            phone:LoginAccount,
            password: "",
            loginMac: localStorage.uuid,
            client: "Mobile",
            version: localStorage.getInfo,
            keying: LoginKeying,
            code: LoginCode,
            reconnection:true
          }
        }
      }else {
        if(LoginMode === 'passLogin'){
          let LoginAccount = localStorage.getItem('LoginAccount')
          let reg = /\b1[3456789]\d{9}\b/g
          let val = reg.test(LoginAccount)
          let pass = localStorage.getItem('LoginPassword')
          let LoginPassword = EP.decrypt(pass,'')
          if(val){
            actions = {
              action: "login",
              reqID: 117,
              data: {
                loginType:1,
                phone:LoginAccount,
                account: '',
                password: LoginPassword,
                loginMac: localStorage.uuid,
                client: "Mobile",
                version: localStorage.getInfo,
                keying: "",
                code: "",
                reconnection:true
              }
            }
          }else {
            actions = {
              action: "login",
              reqID: 117,
              data: {
                loginType:0,
                phone:'',
                account: LoginAccount,
                password: LoginPassword,
                loginMac: localStorage.uuid,
                client: "Mobile",
                version: localStorage.getInfo,
                keying: "",
                code: "",
                reconnection:true
              }
            }
          }
        }else {
          let LoginAccount = localStorage.getItem('LoginAccount')
          let LoginCode = localStorage.getItem('LoginCode')
          let LoginKeying = localStorage.getItem('LoginKeying')
          actions = {
            action: "login",
            reqID: 117,
            data: {
              loginType:2,
              account: '',
              phone:LoginAccount,
              password: "",
              loginMac: localStorage.uuid,
              client: "Mobile",
              version: localStorage.getInfo,
              keying: LoginKeying,
              code: LoginCode,
              reconnection:true
            }
          }
        }
      }
      that.global.ws.send(JSON.stringify(actions))
      localStorage.duan = 'unDuan'
    },
    Masg(e){
      let list=e.data.split(':');
      this.num=0;
      let data=e.data;
      if(list.length===2){
        data=list[1]
      }
      let s = this.zip(data);
      let obj = JSON.parse(s);
      // console.log(obj)
      if(obj !== null){
        for(let i=0,len = obj.length;i<len;i++){
          this.$set(this.tickCopy,obj[i]['instrumentid'],obj[i]);
        }
        this.appTick = obj;
      }
      localStorage.tickCopy = JSON.stringify(this.tickCopy);
    },
    zip(b64Data){
      let strData   = atob(b64Data);
      let Data  = strData.split('').map(function(x){return x.charCodeAt(0);});
      let atrData   = new Uint8Array(Data);
      let data    = pako.inflate(atrData);
      strData   = String.fromCharCode.apply(null, new Uint16Array(data));
      return strData;
    },
    localOftenSk(url) {
      let that = this;
      if ("WebSocket" in window) {
        console.log(url)
        that.oftenSk = new WebSocket('ws://'+url+':7777/ws');
        that.global.setWs(that.oftenSk);
        // that.global.ws.
        that.global.ws.onmessage = function(res) {
          that.oftenSkOnmessage (res)
        };
        that.global.ws.onclose = function() {
          console.log('短线')
        };
        that.global.ws.onerror = function() {
          console.log('短线')
        };
        // 7777心跳
        that.setSendTimeEle()
      } else {
        // 浏览器不支持 WebSocket
        console.log("您的浏览器不支持 WebSocket!");
      }
    },
    // 7777接收数据
    oftenSkOnmessage(e){
      let that = this
      this.redata = JSON.parse(e.data)
      console.log(this.redata,'APP')
      // 登录APP
      if(this.redata.action === "login" && this.redata.data.code === 0){
        localStorage.setItem('token', that.redata.data.object.token)
      }
      if (this.redata.action === "discarded") {
        if(this.redata.data.code === 0){
          Toast({
            duration:1000,
            message:'网络异常，即将退出登录！',
            position:'bottom',
            onClose() {
              that.Logout()
            }
          })
        }
      }
      //   if (this.redata.data.code === 0) {
      //     let bankAccount = EP.encrypt(that.redata.data.object.bankAccount,'')
      //     let idcard = EP.encrypt(that.redata.data.object.idcard,'')
      //     localStorage.setItem('Flag', 'isLogin')
      //     localStorage.setItem('name', that.redata.data.object.name)
      //     localStorage.setItem('phone', that.redata.data.object.phone)
      //     localStorage.setItem('account',that.redata.data.object.account)
      //     localStorage.setItem('idcard',idcard)
      //     localStorage.setItem('idcardtype',that.redata.data.object.idcardtype)
      //     localStorage.setItem('bank',that.redata.data.object.bank)
      //     localStorage.setItem('bankAccount',bankAccount)
      //     localStorage.setItem('uid', that.redata.data.object.id)
      //     localStorage.setItem('status', that.redata.data.object.status)
      //     localStorage.setItem('token', that.redata.data.object.token)
      //     localStorage.setItem('isDrawPass', that.redata.data.object.isDrawPass)
      //     let discount = EP.encrypt(that.redata.data.object.discount,'')
      //     localStorage.setItem('discount',discount)
      //     if(!localStorage.LoginAccount){
      //       this.$router.push({
      //         name:'home',
      //         params:{
      //           showRisk:'1'
      //         }
      //       })
      //     }else {
      //       this.$router.push({
      //         name:'home',
      //         params:{
      //           status:this.redata.data.object.status,
      //         }
      //       })
      //     }
      //   } else if(that.redata.data.msg !== null) {
      //     Toast({
      //       position:'bottom',
      //       message:that.redata.data.msg,
      //       duration:2000
      //     })
      //   }
      // }
      // 不同设备登录
      if(this.redata.action === "mutipointlogin"){
        localStorage.setItem('WhetherReconnect',true)
        Toast({
          message:'您的账号已在别的设备登录，请确认是否本人操作',
          duration:2000,
          position:'bottom',
          onOpened() {
            localStorage.jix = 'jix'
            that.Logout()
          }
        })
      }
      if(this.redata.action === "documentary"){
        if(this.redata.data.code === 0){
          Toast({
            message:'跟单成功',
            duration:2000,
            position:'bottom',
          })
        }else {
          Toast({
            message:that.redata.data.msg,
            duration:2000,
            position:'bottom',
          })
        }

      }
    },
    // 心跳
    setSendTimeEle(msg) {
      let that = this;
      clearInterval(that.sendTime);
      that.sandTime = setInterval(that.balanceTimeData,6000)
    },
    balanceTimeData(){
      // console.log('心跳')
      this.global.ws.onopen = this.onopenSand()
        // console.log('交易断了')
        if(this.global.ws.readyState !== 1 && localStorage.Flag === 'isLogin'){
            // console.log('重连1111')
          this.Reconnect()
        }
    },
    onopenSand () { // websocket连接后发送数据(send发送)
      let actions;
      let that = this
      actions = {
        action: "heartbeat",
        reqID: 199,
      }
      console.log(that.global.ws.readyState !== 1)
      if(that.global.ws.readyState !== 1){
      }else {
        that.global.ws.send(JSON.stringify(actions))
      }
    },
    textEdition(data){
      let this_=this;
      this.$axios.get('http://'+this.discUrl+':7777/api/version/lastVersion?fileType=1').then(res => {
        this_.VerData=res;
        console.log(this_.VerData)
        let Ver='';
        //
        // this_.version.New=res.data.object.fileDition;
        // this_.showselect=true;
        // this_.downloadC();
        //
        // return;
        plus.runtime.getProperty('H5727BBB2',function(inf){
          Ver=inf.version;
          // Ver='1.0.0';
          this_.version.old=inf.version;
          localStorage.version=this_.version.old;
          // alert(localStorage.version);
          let Ver2=res.data.object.fileDition;
          let list1=Ver.split('.');
          let list2=Ver2.split('.');
          list1=Number(list1[0]+list1[1]+list1[2]);
          list2=Number(list2[0]+list2[1]+list2[2]);
          if(list1<list2){
            this_.version.New=res.data.object.fileDition;
            this_.showselect=true;
          }else {
            if(data==1){
              plus.nativeUI.showWaiting("当前版本已是最新，无需更新...");
              setTimeout(()=>{
                plus.nativeUI.closeWaiting();
              },1000)
            }
          }
        });

      })
    },
    installWgt(path){
      let this_=this;
      // plus.nativeUI.showWaiting("应用更新中...");
      this_.type=2;
      plus.runtime.install(path,{},function(){
        // plus.nativeUI.closeWaiting();
        // plus.nativeUI.alert("更新成功！",function(){
        //   plus.runtime.restart();
        // });
        // plus.nativeUI.showWaiting("应用更新成功...");
        this_.type=3;
        plus.runtime.restart();
        // alert(localStorage.version);
        setTimeout(()=>{
          this_.showselect=false;
          this_.version.New=res.data.object.fileDition;
        },400)
      },function(e){
        // plus.nativeUI.closeWaiting();
        plus.nativeUI.alert("更新失败！["+e.code+"]："+e.message);
      });
    },
    downloadC(){
      let this_=this;
      var wgtUrl='http://47.110.226.247/'+this.VerData.data.object.fileAdress;
      // console.log(wgtUrl)
      // plus.nativeUI.showWaiting("更新下载中，请稍等...");
      this_.type=1
      let download=plus.downloader.createDownload( wgtUrl, {filename:"_doc/update/"}, function(d,status){
        if ( status === 200 ) {
          this_.installWgt(d.filename); // 安装wgt包
        } else {
          plus.nativeUI.alert("更新下载失败");
        }
        // alert(1)
        // plus.nativeUI.closeWaiting();
        // plus.downloader.enumerate(function(tasks){
        //   alert("Unfinished task count: " + tasks.length);
        // });
        // alert(7)
      });
      download.start();
      // alert(JSON.stringify(download));
      download.addEventListener("statechanged", function () {
        if(download.totalSize>0){
          if(download.downloadedSize==0){
            this_.ProgressPercent='0%';
          }else {
            if(this_.type!==0){
              this_.ProgressPercent=(Number(download.downloadedSize)/Number(download.totalSize)*100).toFixed(0)+'%';
              document.getElementsByClassName('ProgressBar')[0].style.width=this_.ProgressPercent;
            }
          }
        }
      }, false);
      // let download=plus.downloader.Download;
      // alert(7)
      // console.log(JSON.stringify(download))
    },
    stopDownload(){
      this.showselect=false;
    },
  },
}
</script>

<style lang="scss">
  *{
    /*-webkit-touch-callout:none;!*系统默认菜单被禁用*!*/
    -webkit-user-select:none;/*webkit浏览器*/
    -khtml-user-select:none;/*早起浏览器*/
    -moz-user-select:none;/*火狐浏览器*/
    -ms-user-select:none;/*IE浏览器*/
    user-select:none;/*用户是否能够选中文本*/
    -webkit-user-select: none;
    -moz-user-select: none;
  }
  @import "./style/theme2";
  /*@import "assets/font/font.css";*/
  @font-face {
    font-family: 'iconfont';
    src: url('./assets/font/iconfont.eot');
    src: url('./assets/font/iconfont.eot?#iefix') format('embedded-opentype'),
    url('./assets/font/iconfont.woff2') format('woff2'),
    url('./assets/font/iconfont.woff') format('woff'),
    url('./assets/font/iconfont.ttf') format('truetype'),
    url('./assets/font/iconfont.svg#iconfont') format('svg');
  }
  #app{
    width: 100%;
    height: 100%;
    background-color: #ffffff;
    color: #4F4F4F;
    position: relative;
    overflow: hidden;
    .weww {
      text-align: center;
      z-index: 9999;
      background-color: #6a6a6a;
      color: #FFF;
      width:tovw(200);
      border-radius: tovw(10);
      font-size: tovw(18);
      position: absolute;
      padding: tovw(10);
      top: 50%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
    }
    .showSelect{
      position: absolute;
      width: 100vw;
      height: 100vh;
      top: 0;
      left: 0;
      background-color: rgba(0, 0, 0, 0.3);
      z-index: 999;
      .content {
        width: tovw(250);
        height:tovw(160);
        background-color: #FFF;
        border-radius: tovw(14);
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        .title {
          margin-top: tovw(5);
          width: 100%;
          height: tovw(90);
          text-align: center;
          &>p:nth-child(1){
            height: tovw(45);
            color: rgba(34, 34, 34, 1);
            font-size: tovw(16);
            line-height: tovw(45);
            font-weight: 600;
          };
          &>p:nth-child(2){
            height: tovw(45);
            color: rgba(34, 34, 34, 1);
            font-size: tovw(16);
            line-height: tovw(45);
          }
        }
        .submit {
          margin-top: tovw(15);
          width: 100%;
          height: tovw(40);
          font-size: 0;
          &>span:nth-child(1) {
            font-size: tovw(14);
            display: inline-block;
            height: 100%;
            width: 50%;
            text-align: center;
            color: rgba(102, 102, 102, 1);
            &>span {
              display: inline-block;
              width: 70%;
              height: tovw(32);
              border-radius: tovw(17);
              text-align: center;
              margin-top: tovw(4);
              line-height: tovw(33);
              border: 1px solid rgba(102, 102, 102, 1);
            }
          }
          &>span:nth-child(2) {
            font-size: tovw(14);
            display: inline-block;
            height: 100%;
            width: 50%;
            text-align: center;
            &>span {
              display: inline-block;
              width: 70%;
              height: tovw(32);
              border-radius: tovw(17);
              text-align: center;
              margin-top: tovw(4);
              line-height: tovw(33);
              color: #FFF;
              background-color: rgba(25, 136, 255, 1);
            }
          }
        }
        .Progress {
          position: absolute;
          left: 50%;
          bottom: tovw(20);
          transform: translateX(-50%);
          margin-top: tovw(15);
          width: tovw(215);
          height: tovw(32);
          background-color: rgba(227, 227, 227, 1);
          .ProgressBar{
            width: 0%;
            height: 100%;
            background-color: rgba(25, 136, 255, 1);
          }
          .con {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            line-height: tovw(32);
            text-align: center;
            font-size: tovw(12);
            color: rgba(102, 102, 102, 1);
          }
          .con2 {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            line-height: tovw(32);
            text-align: center;
            font-size: tovw(16);
            color: rgba(102, 102, 102, 1);
          }
        }
      }
    }
  }
  *{
    -webkit-touch-callout: none;
    -webkit-user-select:none;
    -moz-user-select:none;
    -ms-user-select:none;
    user-select:none;
  }
  input,textarea{
    -webkit-user-select:auto;
  }
  .fade-enter-active, .fade-leave-active {
    transition: opacity .2s;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }

</style>



