import Vue from 'vue'
import VueI18n from 'vue-i18n';
Vue.use(VueI18n)

// 引入语言配置
import zh from './config/zh'
import en from './config/en'
import ja from './config/ja'
// 创建vue-i18n
const i18n  = new VueI18n({
  locale:localStorage.getItem('i18n'),
  messages:{
    zh,//中文
    en,//英文
    ja,//日文
  },
  // silentTranslationWarn: true,
})
export default i18n;



