  Risk20:'　　2. Your computer, operating system and other software and hardware environments may be attacked by viruses or network hackers due to their own reasons, which may affect your trading system connection and make your investment decisions unable to be executed correctly or in time. When you use this system to conduct investment transactions, you should actively take security measures, such as: regular maintenance of mobile phones and networked devices, use of anti-virus and anti-hacking products, and proper custody of personal data to prevent various risks that may occur in electronic transactions',
