  Risk22:'　　4. The system server of this business is synchronized with Beijing time, please synchronize the time of the machine with the client installed. Because the time is not synchronized may lead to the risk of trading.',
  Risk23:'　　5. The system of this business provides some trading methods that are not provided by the exchange. The user should understand the operating mechanism in detail before using it, and the resulting trading results shall be borne by the user.',
  Risk24:'For the risks brought by the above uncertain factors, it may affect your investment, you should fully understand and bear all the losses caused thereby.',
