  Risk27:'　　2. The price of this trading center is based on the price of the international futures market and continuously quotes the product quotation of the trading center. The price may have a slight gap with the quotations of other channels, and the trading center cannot guarantee that the above-mentioned transaction prices are completely consistent with other markets.',
  Risk28:'　　3. The market price you submit in the trading system is single transaction, that is, irrevocable, you must accept the risks that this method may bring.',
