  Risk30:'　　5. Your investment transaction must be based on your own independent decision. Any analysis and information about the market provided by the trading center, authorized service agencies (and their branches), brokers and the staff of the aforementioned entities are for investors\' reference only, and does not constitute any offer or promise. The transaction risk caused by this shall be borne by you.',
  Risk31:'　　6. In the process of investment transactions, due to technical failures or other force majeure factors, accidental wrong quotations may occur. The trading center may correct the wrong price and the profit and loss caused by the wrong price afterwards. The resulting transaction risk is caused by You bear it yourself.',
  Risk32:'　　7. In the process of electronic transactions, due to technical failures or other force majeure factors, accidental wrong quotations may occur. The trading market has the right to correct the wrong price and the profit and loss caused by the wrong price afterwards. The customer should accept the relevant error correction behavior. The transaction risk caused by this is borne by the customer.',
  Risk33:'　　8. You are obligated to keep the relevant passwords set, saved and modified by yourself. All system operations performed through your account and password are regarded as your own behavior by the trading center, and all consequences arising from the disclosure, theft or improper use of your password shall be borne by you. For the risks brought by the above uncertain factors, it may affect your investment, you should fully understand and bear all the losses caused thereby.',
  Risk34:'　　(5) Force majeure risk',
