<template>
<div class="price">
  <div class="choose">
    <router-link to="/demo/market" class="choose1">{{$t('Markets')}}</router-link>
<!--    <router-link to="/demo/selfchoose" class="choose2">自选</router-link>-->
  </div>
  <div class="content">
    <router-view :Tick="appTick" :dataList="dataList" @DataList = "DataList"  :tickCopy="TickCopy"></router-view>
  </div>
</div>
</template>
<script>
  import { Toast } from 'vant';
  import EP from '../../utils/encryption'
export default {
  data () {
    return {
      active: 0,
      // Tick:[],
      dataList:[],
      TickCopy:{},        //保存最后一次tick
      // 心跳
      sendTime:null,
      // 断线重连计时器
      ReconnectTime:null,
      websock:null
    }
  },
  props:{
    appTick:{

    },
    tickCopy:{
      type:Object
    }
  },
  // 关闭心跳计时器
  beforeDestroy() {
    if(this.sandTime) {
      clearInterval(this.sandTime);
    }
    if(this.ReconnectTime) {
      clearInterval(this.ReconnectTime);
    }
  },
  destroyed(){
    // console.log('demo销毁了')
  },
  created () {
    let that = this
    this.discUrl = localStorage.getItem('discUrl')
    // console.log(this.$store.state.discUrl);
    if(localStorage.tickCopy){
      this.TickCopy = JSON.parse(localStorage.tickCopy)
    }
    clearInterval(that.ReconnectTime);
    that.ReconnectTime = setInterval(function () {
      if(that.global.ws.readyState != 1){
        console.log('断了')
        that.localSocket()
      }else {
        // this.ReconnectTime = setInterval(this.localSocket(),2000)
      }
    },1000)
    this.setSendTimeEle()
  },
  methods:{
    localSocket() {
      let that = this;
      console.log(that.discUrl)
      that.websock = new WebSocket('ws://'+that.discUrl+':7777/ws');
      that.global.setWs(that.websock);
      that.global.ws.onopen = this.setReconnect
      that.global.ws.onmessage = function (e) {
        that.getReconnect(e)
      }
    },
    setReconnect(){
      let actions;
      let that = this
      let LoginMode = localStorage.getItem('LoginMode')
      if(LoginMode === 'passLogin'){
        let LoginAccount = localStorage.getItem('LoginAccount')
        let pass = localStorage.getItem('LoginPassword')
        let LoginPassword = EP.decrypt(pass,'')
        actions = {
          action: "login",
          reqID: 117,
          data: {
            account: LoginAccount,
            password: LoginPassword,
            loginMac: "",
            client: "Mobile",
            version: "",
            keying: "",
            code: "",
            reconnection:true
          }
        }
      }else {
        let LoginAccount = localStorage.getItem('LoginAccount')
        let LoginCode = localStorage.getItem('LoginCode')
        let LoginKeying = localStorage.getItem('LoginKeying')
        actions = {
          action: "login",
          reqID: 117,
          data: {
            account: LoginAccount,
            password: "",
            loginMac: "",
            client: "Mobile",
            version: "",
            keying: LoginKeying,
            code: LoginCode,
            reconnection:true
          }
        }
      }
      this.global.ws.send(JSON.stringify(actions))
    },
    getReconnect(e){


      let that = this
      this.redata = JSON.parse(e.data)
      console.log(this.redata)
      if (this.redata.action === "discarded") {
        if(this.redata.data.code === 0){
          Toast({
            duration:1000,
            message:that.$t('noNetwork'),
            onClose() {
              that.Logout()
            }
          })
        }
      }
      if(this.redata.action === "mutipointlogin"){
        Toast({
          message:that.$t('jixMessage'),
          duration:2000,
          position:'bottom',
          onOpened() {
            localStorage.jix = 'jix'
            that.Logout()
          }
        })
      }else {
        if(this.redata.action === 'login' && this.redata.data.code === 0){
          localStorage.setItem('allClose',1)
          localStorage.setItem('token',this.redata.data.object.token)
        }
      }
    },
    // 心跳
    setSendTimeEle(msg) {
      let that = this;
      if (that.global.ws && that.global.ws.readyState == 1) {
        clearInterval(that.sendTime);
        that.sandTime = setInterval(that.balanceTimeData,30000)
      }
    },
    balanceTimeData(){
      console.log('心跳测试')
      this.global.ws.onopen = this.onopenSand()
    },
    onopenSand () { // websocket连接后发送数据(send发送)
      let actions;
      let that = this
      actions = {
        action: "heartbeat",
        reqID: 199,
      }
      that.global.ws.send(JSON.stringify(actions))
    },
    Drepush(){
      this.getContract()
    },
    DataList(data){
      this.dataList = data;
    }
  }
}

</script>

<style lang="scss" scoped>
  @import "../../style/theme2";
  .price {
    width: tovw(375);
    /*height: tovw(612);*/
    /*padding-top: tovw(25);*/
    display: flex;
    position: fixed;
    top:0;
    bottom: tovw(55);
    z-index: 3;
    @media only screen and (device-width: 375px) and (device-height: 812px){
      bottom: tovw(73);
    }
    @media only screen and (device-width: 414px) and (device-height: 896px){
      bottom: tovw(73);
    }
    flex-direction: column;
    .choose {
      width: 100%;
      height: tovw(45);
      background-color: #FFF;
      font-size: 0;
      text-align: left;
      .router-link-active {
        color: #000;
        /*border-bottom:tovw(2) solid #FF7100;*/
        border-radius: tovw(2);
      }
      a {
        color:#8C8B8B;;
      }
      .choose1 {
        font-family: 'siYuan';
        display: inline-block;
        font-size: tovw(18);
        line-height: tovw(25);
        margin: tovw(10) tovw(16);
        font-weight: bold;
        /*border-bottom: tovw(2) solid #71d3ff;*/
      }
      .choose2 {
        font-family: 'iconfont'!important;
        display: inline-block;
        font-size: tovw(18);
        margin: tovw(10) tovw(12);
        line-height: tovw(25);
        /*border-bottom: tovw(2) solid #71d3ff;*/
      }
    }
    .content {
      flex: 1;
      width: 100%;
      background-color: #FFF;
      /*background-color: #f21328;*/
      z-index: 1;
    }
    @media screen and (orientation:landscape){
      .content{
        width: 0;
        height: 0;
      }
      .choose{
        width: 0;
        height: 0;
      }

    }
  }
</style>



