import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isLogin: false,
    // 登录环境  模拟盘fictitious   实盘reality
    environment:'reality',
    discUrl:'121.43.137.150',
    diUrl:'121.43.137.150',
    // discUrl:'192.168.5.9',
    active:0,
    // discUrl:'192.168.5.30',
    changeSave: 'false',
  },
  getters: {
    isLogin: state => state.isLogin,
    diUrl: state => state.diUrl,
    changeSave: state => state.changeSave,
  },
  mutations: {
    userStatus (state, flag) {
      state.isLogin = flag
    },
    changeSave (state,Change) {
        state.changeSave=Change
    },
    changeEnvironment(state, disc){
      state.environment = disc
    },
    changeUrl(state, disc){
      state.discUrl = disc
    },
    changeActive(state, act){
      state.active = act
    }
  },
  actions: {
    setUser ({commit}, flag) {
      commit('userStatus', flag)
    },
    // setChange ({commit},Change) {
    //   commit('changeSave',Change)
    // }
  }
})



