  //   if(!first){
  //     first = new Date().getTime();//记录第一次按下回退键的时间
  //     mui.toast("再按一次退出应用");
  //
  //     console.log(url+'/home')
  //     console.log(history.length)
  //     console.log(history)
  //     if(history.length>2) {
  //       history.go(-1);
  //     }
  //     setTimeout(function(){
  //       //1s 后清除
  //       first = null;
