    appTick(newVal) {
      // console.log(newVal)
      if (JSON.parse(localStorage.tickCopy) !== "") {
        this.tickCopyData = JSON.parse(localStorage.tickCopy);
      }
      for (let i = 0, len = this.restContract.length; i < len; i++) {
        if (
          this.tickCopyData[this.restContract[i].instrumentid] !== undefined
        ) {
          this.$set(
            this.restContract[i],
            "newPrice",
            this.tickCopyData[this.restContract[i].instrumentid].price.toFixed(
              this.restContract[i].digits
            )
          );
          let diffPriceRate = 0;
          if (this.restContract[i].oldPrice === 0) {
            this.$set(
              this.restContract[i],
              "oldPrice",
              this.tickCopyData[this.restContract[i].instrumentid].price
            );
          }
          if (
            this.tickCopyData[this.restContract[i].instrumentid].preclose === 0
          ) {
            diffPriceRate = "--";
          } else {
            diffPriceRate =
              (
                ((Number(
                  this.tickCopyData[this.restContract[i].instrumentid].price
                ) -
                  Number(
                    this.tickCopyData[this.restContract[i].instrumentid]
                      .preclose
                  )) /
                  this.tickCopyData[this.restContract[i].instrumentid]
                    .preclose) *
                100
              ).toFixed(2) + "%";
          }
          this.$set(this.restContract[i], "newUpsAndDowns", diffPriceRate);
          this.$set(
            this.restContract[i],
            "diffPrice",
            this.tickCopyData[this.restContract[i].instrumentid].price -
              this.tickCopyData[this.restContract[i].instrumentid].preclose
          );
        }
      }
    },
  },
