        if (colee_left2.offsetWidth - colee_left.scrollLeft <= 0) {
          //offsetWidth 是对象的可见宽度
          colee_left.scrollLeft -= colee_left1.offsetWidth; //scrollWidth 是对象的实际内容的宽，不包边线宽度
        } else if (
