    sortArr(arr) {
      console.log(arr);
      let karr = [];
      let groupid = "";
      groupid = arr[0].groupid;
      let max = 0;
      for (let i = 0, len = arr.length; i < len; i++) {
        karr.push(arr[i].instrumentid.replace(/[^\d]/g, ""));
      }
      karr.sort(function (a, b) {
        return a - b;
      });
      max = karr[0];
      return groupid + max;
    },
    newPriceElements(arr) {
      let tickCopy = JSON.parse(localStorage.getItem("tickCopy"));
      let that = this;
      if (tickCopy != null) {
        console.log(tickCopy, 1111);
        for (let i = 0, len = arr.length; i < len; i++) {
          if (tickCopy[arr[i].instrumentid] != null) {
            arr[i].newPrice = tickCopy[arr[i].instrumentid].price.toFixed(
