    },
    // 跳转到公告页
    linkNotice() {
      this.$router.push({ path: "/notice" });
    },
    // 跳转到新闻资讯页
    linkNews() {
      this.$router.push({ path: "/news" });
    },
    // 跳转至拨号界面
    callPhone() {
      // this.$router.push({
      //   name:'homeService'
      // })
      this.$router.push({
        name: "market",
        params: {
          home: 1,
        },
      });
      // window.location.href = 'tel://123456789'
    },
    handdleMsg(msg) {
      let that = this;
      // console.log(that.global.ws);
      that.global.ws.onmessage = function (res) {
        that.websocketonmessage(res);
      };
    },
    // 跳转到财经日历
    linkFinance() {
      // window.location.href = 'https://rili-d.jin10.com/open.php?fontSize=14px&theme=primary'
      // window.location.target = '_Self'
      // window.location.href = 'https://rili-d.jin10.com/open.php?fontSize=14px&theme=primary'
      this.$router.push({
        name: "finance",
      });
    },
    // 获取公告
    getNotice() {
      let that = this;
      this.$axios
        .get(
          "http://" + this.discUrl + ":7777/api/news?type=0&page=0&pageSize=3"
        )
        .then((res) => {
          if (res.data.code === 0 && res.data.object.data != null) {
            for (let i = 0, len = res.data.object.data.length; i < len; i++) {
              let thisTowDate = new Date(res.data.object.data[i].time);
              let currentTowTime =
                (thisTowDate.getMonth() + 1).toString().padStart(2, "0") +
                "-" +
                thisTowDate.getDate().toString().padStart(2, "0") +
                " " +
                thisTowDate.getHours().toString().padStart(2, "0") +
                ":" +
                thisTowDate.getMinutes().toString().padStart(2, "0");
              res.data.object.data[i].showTime = currentTowTime;
            }
            this.NoticeList = res.data.object.data;
          }
        });
    },
    noticeDetails(idx) {
      let that = this;
      this.showNoFollow = true;
      this.noticeTitle = this.NoticeList[idx].title;
      this.noticeContent = this.NoticeList[idx].content;
      console.log(this.NoticeList[idx]);
      // this.$router.push({
      //   name:'noticeDetails',
      //   params:{
      //     data:that.NoticeList[idx]
      //   }
      // })
    },
    confirmNoFollow() {},
    getroadList() {
      const _self = this;
      let that = this;
      this.$axios
        .get(
          "http://" +
            this.discUrl +
            ":7777/api/news?type=1&page=" +
            this.pageIndex +
            "&pageSize=5"
        )
        .then((response) => {
          if (response.data.code === 0 && response.data.object.data != null) {
            let rows = response.data.object.data;
            console.log(rows, "rows");
            for (let i = 0, len = rows.length; i < len; i++) {
              let thisTowDate = new Date(rows[i].time);
              rows[i].month = (thisTowDate.getMonth() + 1)
                .toString()
                .padStart(2, "0");
              rows[i].date = thisTowDate.getDate().toString().padStart(2, "0");
              rows[i].currentTowTime =
                thisTowDate.getHours().toString().padStart(2, "0") +
                ":" +
                thisTowDate.getMinutes().toString().padStart(2, "0") +
                ":" +
                thisTowDate.getSeconds().toString().padStart(2, "0");
              // let value = rows[i].content
              let reg = /https|<br|jpeg|jpg|png|http/g;
              if (reg.test(rows[i].content)) {
                rows[i].show = false;
              } else {
                rows[i].show = true;
              }
              // console.log(rows[i].content,'rows')
              let to = localStorage.i18n;
              if(to !=='zh'){
                $.ajax({
                  url: "http://openapi.youdao.com/api",
                  type: "post",
                  dataType: "jsonp",
                  data: {
                    q: rows[i].content,
                    appKey: this.appKey,
                    salt: this.salt,
                    from: this.from,
                    to: to,
                    sign: md5(
                      this.appKey + rows[i].content + this.salt + this.key
                    ),
                  },
                  success: function (data) {
                    console.log('data :>> ', data);
                    // console.log(data,'rows')
                    rows[i].content = data.translation[0];
                    // vm.$set(vm.$data, 'translatedText', data.translation[0])
                  },
                });
              }
           
            }
            if (rows == null || rows.length === 0) {
              // 加载结束
              that.upFinished = true;
              return;
            }
            if (rows.length < 5) {
              // 最后一页不足10条的情况
              that.upFinished = true;
            }
            // 处理数据
            if (that.pageIndex === 0) {
              that.NewList = rows;
            } else {
              that.NewList = that.NewList.concat(rows);
            }
          }
        })
        .finally(() => {
          that.isUpLoading = false;
        });
    },
    // 上拉加载请求方法
    onLoadList() {
      this.pageIndex++;
      this.getroadList();
    },
    // 跳转详情页
    handleRoadDetail(activity) {
      this.$router.push({
        path: "roaddetail",
        query: {
          id: activity && activity.id,
        },
      });
    },
    textTranslate: function (text, to) {
      let vm = this;
      $.ajax({
        url: "http://openapi.youdao.com/api",
        type: "post",
        dataType: "jsonp",
        data: {
          q: text,
          appKey: this.appKey,
          salt: this.salt,
          from: this.from,
          to: to,
          sign: md5(this.appKey + text + this.salt + this.key),
        },
        success: function (data) {
          console.log(data);
          // vm.$set(vm.$data, 'translatedText', data.translation[0])
        },
      });
    },
  },
};
</script>

<style scoped lang="scss">
@import "../../style/home";
</style>



